// Deal by ID API v1
// GET /api/v1/deals/[dealId] - Get deal details
// PUT /api/v1/deals/[dealId] - Update deal
// DELETE /api/v1/deals/[dealId] - Delete deal
// PATCH /api/v1/deals/[dealId] - Move deal to different stage

import { NextRequest, NextResponse } from 'next/server'
import { supabaseAdmin } from '@/lib/supabase/server'

// GET - Get deal details with activities
export async function GET(
  request: NextRequest,
  { params }: { params: Promise<{ dealId: string }> }
) {
  try {
    const { dealId } = await params
    const { searchParams } = new URL(request.url)
    const includeActivities = searchParams.get('includeActivities') !== 'false'
    const includeProducts = searchParams.get('includeProducts') === 'true'
    
    // Get deal
    const { data: deal, error } = await supabaseAdmin
      .from('Deal')
      .select(`
        *,
        contact:Contact(id, firstName, lastName, email, phone, company, status, tags),
        stage:PipelineStage(id, name, color, stageType, winProbability),
        pipeline:Pipeline(id, name, icon, color)
      `)
      .eq('id', dealId)
      .single()
    
    if (error || !deal) {
      return NextResponse.json(
        { success: false, error: 'Deal not found' },
        { status: 404 }
      )
    }
    
    // Get activities
    if (includeActivities) {
      const { data: activities } = await supabaseAdmin
        .from('DealActivity')
        .select('*')
        .eq('dealId', dealId)
        .order('createdAt', { ascending: false })
        .limit(50)
      
      deal.activities = activities || []
    }
    
    // Get products
    if (includeProducts) {
      const { data: products } = await supabaseAdmin
        .from('DealProduct')
        .select('*')
        .eq('dealId', dealId)
      
      deal.products = products || []
    }
    
    return NextResponse.json({
      success: true,
      data: deal,
    })
  } catch (error: any) {
    console.error('GET /api/v1/deals/[dealId] error:', error)
    return NextResponse.json(
      { success: false, error: error.message || 'Failed to fetch deal' },
      { status: 500 }
    )
  }
}

// PUT - Update deal
export async function PUT(
  request: NextRequest,
  { params }: { params: Promise<{ dealId: string }> }
) {
  try {
    const { dealId } = await params
    const body = await request.json()
    
    // Get current deal
    const { data: currentDeal } = await supabaseAdmin
      .from('Deal')
      .select('*')
      .eq('id', dealId)
      .single()
    
    if (!currentDeal) {
      return NextResponse.json(
        { success: false, error: 'Deal not found' },
        { status: 404 }
      )
    }
    
    // Build update object
    const updateFields = [
      'name', 'description', 'value', 'currency', 'probability',
      'expectedCloseDate', 'actualCloseDate', 'assignedTo',
      'source', 'sourceDetails', 'priority', 'customFields', 'tags',
      'lossReason', 'lossNotes', 'winNotes', 'nextFollowUpAt', 'contactId'
    ]
    
    const updateData: any = { lastActivityAt: new Date().toISOString() }
    for (const field of updateFields) {
      if (body[field] !== undefined) {
        updateData[field] = body[field]
      }
    }
    
    // Update deal
    const { data: deal, error } = await supabaseAdmin
      .from('Deal')
      .update(updateData)
      .eq('id', dealId)
      .select(`
        *,
        contact:Contact(id, firstName, lastName, email, phone),
        stage:PipelineStage(id, name, color, stageType),
        pipeline:Pipeline(id, name, icon, color)
      `)
      .single()
    
    if (error) throw error
    
    // Log value change activity
    if (body.value !== undefined && body.value !== currentDeal.value) {
      await supabaseAdmin
        .from('DealActivity')
        .insert({
          dealId,
          activityType: 'value_change',
          title: 'Deal value changed',
          fromValue: currentDeal.value,
          toValue: body.value,
        })
    }
    
    return NextResponse.json({
      success: true,
      data: { deal },
    })
  } catch (error: any) {
    console.error('PUT /api/v1/deals/[dealId] error:', error)
    return NextResponse.json(
      { success: false, error: error.message || 'Failed to update deal' },
      { status: 500 }
    )
  }
}

// PATCH - Move deal to different stage or update status
export async function PATCH(
  request: NextRequest,
  { params }: { params: Promise<{ dealId: string }> }
) {
  try {
    const { dealId } = await params
    const body = await request.json()
    const { stageId, status, stagePosition, lossReason, winNotes } = body
    
    // Get current deal
    const { data: currentDeal } = await supabaseAdmin
      .from('Deal')
      .select('*, stage:PipelineStage(id, name)')
      .eq('id', dealId)
      .single()
    
    if (!currentDeal) {
      return NextResponse.json(
        { success: false, error: 'Deal not found' },
        { status: 404 }
      )
    }
    
    const updateData: any = { lastActivityAt: new Date().toISOString() }
    
    // Handle stage change
    if (stageId && stageId !== currentDeal.stageId) {
      // Get new stage
      const { data: newStage } = await supabaseAdmin
        .from('PipelineStage')
        .select('id, name, stageType, winProbability')
        .eq('id', stageId)
        .single()
      
      if (!newStage) {
        return NextResponse.json(
          { success: false, error: 'Invalid stage' },
          { status: 400 }
        )
      }
      
      updateData.stageId = stageId
      updateData.probability = newStage.winProbability
      
      // Auto-update status based on stage type
      if (newStage.stageType === 'won') {
        updateData.status = 'won'
        updateData.actualCloseDate = new Date().toISOString().split('T')[0]
        if (winNotes) updateData.winNotes = winNotes
      } else if (newStage.stageType === 'lost') {
        updateData.status = 'lost'
        updateData.actualCloseDate = new Date().toISOString().split('T')[0]
        if (lossReason) updateData.lossReason = lossReason
      } else if (currentDeal.status !== 'open') {
        updateData.status = 'open'
        updateData.actualCloseDate = null
      }
      
      // Get max position in new stage if not specified
      if (stagePosition === undefined) {
        const { data: maxPosResult } = await supabaseAdmin
          .from('Deal')
          .select('stagePosition')
          .eq('stageId', stageId)
          .order('stagePosition', { ascending: false })
          .limit(1)
          .single()
        
        updateData.stagePosition = (maxPosResult?.stagePosition || 0) + 1
      }
      
      // Log stage change activity
      await supabaseAdmin
        .from('DealActivity')
        .insert({
          dealId,
          activityType: 'stage_change',
          title: `Moved to ${newStage.name}`,
          fromStageId: currentDeal.stageId,
          toStageId: stageId,
        })
    }
    
    // Handle status change
    if (status && status !== currentDeal.status) {
      updateData.status = status
      
      if (status === 'won' || status === 'lost') {
        updateData.actualCloseDate = new Date().toISOString().split('T')[0]
      }
      
      if (status === 'lost' && lossReason) {
        updateData.lossReason = lossReason
      }
      
      if (status === 'won' && winNotes) {
        updateData.winNotes = winNotes
      }
      
      // Log status change activity
      await supabaseAdmin
        .from('DealActivity')
        .insert({
          dealId,
          activityType: 'status_change',
          title: `Status changed to ${status}`,
          fromStatus: currentDeal.status,
          toStatus: status,
        })
    }
    
    // Handle position change
    if (stagePosition !== undefined) {
      updateData.stagePosition = stagePosition
    }
    
    // Update deal
    const { data: deal, error } = await supabaseAdmin
      .from('Deal')
      .update(updateData)
      .eq('id', dealId)
      .select(`
        *,
        contact:Contact(id, firstName, lastName, email, phone),
        stage:PipelineStage(id, name, color, stageType),
        pipeline:Pipeline(id, name, icon, color)
      `)
      .single()
    
    if (error) throw error
    
    return NextResponse.json({
      success: true,
      data: { deal },
    })
  } catch (error: any) {
    console.error('PATCH /api/v1/deals/[dealId] error:', error)
    return NextResponse.json(
      { success: false, error: error.message || 'Failed to move deal' },
      { status: 500 }
    )
  }
}

// DELETE - Delete deal
export async function DELETE(
  request: NextRequest,
  { params }: { params: Promise<{ dealId: string }> }
) {
  try {
    const { dealId } = await params
    const { searchParams } = new URL(request.url)
    const hardDelete = searchParams.get('hard') === 'true'
    
    if (hardDelete) {
      const { error } = await supabaseAdmin
        .from('Deal')
        .delete()
        .eq('id', dealId)
      
      if (error) throw error
    } else {
      // Soft delete - set status to abandoned
      const { error } = await supabaseAdmin
        .from('Deal')
        .update({ status: 'abandoned' })
        .eq('id', dealId)
      
      if (error) throw error
    }
    
    return NextResponse.json({
      success: true,
      message: 'Deal deleted successfully',
    })
  } catch (error: any) {
    console.error('DELETE /api/v1/deals/[dealId] error:', error)
    return NextResponse.json(
      { success: false, error: error.message || 'Failed to delete deal' },
      { status: 500 }
    )
  }
}
